.article {
    background: #fff;
    width: calc(33% - 50px);
    box-shadow: 1px 6px 10px rgba(164, 164, 164, .3);
    margin: 15px;
    
}
@media only screen and (max-width: 600px) {
    .article {
        width: 100%;
    
    }
    
}
.article .thumbnail {
        background-size: cover;
        height: 180px;
        background-position: 100%;
    
}
.article .info {
        margin-top: 20px;
        padding: 0 10px;
    
}
.article .info h2 {
            font-size: 18px;
            line-height: 24px;
            font-weight: 500;
        }
.article .info h2 a {
                color: #119f94;
                text-decoration: none;
            }
.article .info p {
            font-weight: 400;
            padding-bottom: 0;
            color: #808080;
            line-height: 24px
        }